:root {
  --spacing: 8px;
}

.container {
  align-items: center;
  gap: calc(var(--spacing) * 3);
  padding: calc(var(--spacing) * 2);
  flex-direction: column;
  display: flex;
}

.search-form {
  gap: var(--spacing);
  display: flex;
}

.search-form > button {
  padding: var(--spacing) calc(var(--spacing) * 2);
  text-transform: uppercase;
  cursor: pointer;
}

.search-form > input {
  min-width: 240px;
  max-width: 320px;
  padding: var(--spacing);
  font: bold;
  font-size: 32px;
}

.gallery {
  gap: calc(var(--spacing) * 2);
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  display: grid;
}

.gallery img {
  max-width: 100%;
  height: auto;
  display: block;
}

.load-more {
  display: none;
}

.load-more.is-visible {
  display: inline-block;
}

